<template>
	<section class="projects">
    <!-- Section header -->
    <div class="projects-header section-padding">
      <h2>Projects</h2>
      <!-- Progress control -->
      <div class="progress-control">
        <div class="progress-indicator">
          <span v-for="i in projects.length" :key="i" :class="{ current: i === index + 1 }" @click="scrollTo(i - 1)" />
        </div>
        <div class="progress-buttons">
          <span class="prev" @click="scrollTo(index - 1)">&#10094;</span>
          <span class="next" @click="scrollTo(index + 1)">&#10095;</span>
        </div>
      </div>
    </div>
    <!-- Horizontal scrolling list -->
    <div class="projects-scroller" ref="scroller">
      <!-- Projects container -->
      <div class="projects-container" ref="container">
        <!-- Project card -->
        <div v-for="project in projects" :key="project.title" class="project">
          <div class="thumbnail" :style="getBackgroundStyle(project.thumbnail)" />
          <div class="details" :class="{ 'has-link': project.url }">
            <!-- Project title -->
            <h3 class="title">{{ project.title }}</h3>
            <!-- Project description -->
            <div class="description">{{ project.description }}</div>
            <!-- Tags / relevance -->
            <div class="tags" v-if="project.tags">
              <span class="tag" v-for="tag in project.tags" :key="tag">{{ tag }}</span>
            </div>
            <!-- Call to action -->
            <a class="btn btn-visit" v-if="project.url" :href="project.url" rel="noopener noreferrer" target="_blank" disabled>
              View <img src="@/assets/new_tab_gray.svg" alt="Opens in new tab" class="new-tab-icon" />
            </a>
          </div>
        </div>
      </div>
    </div>
	</section>
</template>

<script>
/**
 * title, thumbnail, link, description
 */
export default {
  props: {
    projects: Array
  },
  data() {
    return {
      progress: 0,
      index: 0
    }
  },
  mounted() {
    // Set CSS variable of the number of projects in the scroller
    document.documentElement.style.setProperty('--nr-projects', this.projects.length)
    // Attach scroll listener
    this.$refs.scroller.addEventListener('scroll', this.onScroll)
  },
  methods: {
    getBackgroundStyle(image) {
      return 'background-image: url("' + image + '");'
    },
    onScroll(e) {
      // Calculate the index of the project card that is in view
      this.progress = e.target.scrollLeft / (this.$refs.container.clientWidth - e.target.clientWidth)
      this.progress = Math.min(Math.max(.01, this.progress), 1)
      this.index = Math.ceil(this.progress / (1. / this.projects.length)) - 1
    },
    scrollTo(newIndex) {
      // Cycle indices
      newIndex = newIndex === -1 ? newIndex + this.projects.length : newIndex % this.projects.length
      // Get container padding
      const containerPadding = parseFloat(window.getComputedStyle(this.$refs.container, null).getPropertyValue('padding-left'))
      // Calculate scroll position of card at the provided index
      const newX = newIndex * (1. / this.projects.length) * (this.$refs.container.clientWidth - 2 * containerPadding)
      // Scroll!
      this.$refs.scroller.scrollTo({
        left: newX,
        behavior: 'smooth'
      })
    }
  }
}
</script>

