<template>
  <div id="scroll-label" :class="{ scrolling }" :style="labelStyle">{{ currentIndex }}</div>
</template>

<script lang="ts">
export default {
  data() {
    return {
      elements: [],
      progress: .5,
      scrolling: false,
      currentIndex: '',
      updateLoop: null,
      timer: null,
      label: null
    }
  },
  mounted() {
    this.updateLoop = setInterval(this.initializeIndices, 3000)
  },
  destroyed() {
    // Dettach listeners
    window.removeEventListener('scroll', this.onScroll)
    clearInterval(this.updateLoop)
  },
  methods: {
    initializeIndices() {
      this.label = document.getElementById('scroll-label')
      // Dettach scroll listener
      window.removeEventListener('scroll', this.onScroll)
      // Find elements that want to be in the index
      const elements = document.querySelectorAll('[data-scroll-index]')
      // Find their position and names
      for (const el of elements) {
        this.elements.push({
          name: el.dataset.scrollIndex,
          pos: this.getElementOffset(el)
        })
      }
      // Attach scroll listener
      window.addEventListener('scroll', this.onScroll)
    },
    getViewHeight() {
      return Math.max(document.documentElement.clientHeight, window.innerHeight || 0)
    },
    getScrollAmount() {
      return window.pageYOffset || document.documentElement.scrollTop
    },
    getElementOffset(el) {
      return el.getBoundingClientRect().top + this.getScrollAmount()
    },
    onScroll() {
      // Get the scroll amount from a little above the middle of the view port
      const scrollAmount = this.getScrollAmount() + (this.getViewHeight() / 2.5)
      // Calculate how far we scrolled down the document as a percentage
      this.progress = Math.max(.1, Math.min(scrollAmount / document.body.clientHeight, .9))
      // Set the name of the current index we are scrolling through
      this.currentIndex = this.getClosestIndex(scrollAmount)
      // Indicate we are scrolling
      this.scrolling = true
      // Reset any previous hide timeout
      clearTimeout(this.timer)
      // Set a new timeout to hide the scroll label
      this.timer = setTimeout(() => this.scrolling = false, 100)
    },
    getClosestIndex(pos) {
      if (this.elements.length === 0) return ''
      // First element by default
      let prevName = this.elements[0].name
      for (const index of this.elements) {
        // If we scrolled past this element
        if (index.pos <= pos) prevName = index.name
        // If the next element is not reached yet
        if (index.pos > pos) return prevName
      }
      // If scrolled to the last element
      return prevName
    }
  },
  computed: {
    labelStyle() {
      return `top: ${(this.progress * this.getViewHeight()) - (this.label ? this.label.clientHeight : 0) / 2}px;`
    }
  }
}
</script>

<style lang="scss" scoped>
#scroll-label {
  user-select: none;
  pointer-events: none;
  position: fixed;
  z-index: 1000;
  left: 0;
  opacity: 0;
  transition: opacity .2s ease-in-out;

  &.scrolling {
    opacity: 1;
  }
}
</style>