<template>
  <div class="skewed-bkg" :style="skewStyleBackground">
    <div class="skewed-content" :style="skewStyleForeground">
     <slot />
    </div>
  </div>
</template>

<script lang="ts">
export default {
  props: {
    color: {
      type: String,
      default: '#000'
    },
    angle: {
      type: Number,
      default: -6
    },
    skewContent: {
      type: Boolean,
      default: true
    }
  },
  computed: {
    skewStyleBackground() {
      return `
        background-color: ${this.color};
        transform: skewY(${this.angle}deg);
      `
    },
    skewStyleForeground() {
      return `
        transform: skewY(${this.skewContent ? -this.angle : 0}deg);
        padding: ${Math.abs(Math.tan(this.angle * Math.PI/180)) * 50}vw 0;
      `
    }
  }
}
</script>