<template>
  <div id="app">
    <!-- Portrait picture -->
    <div class="portrait-container" data-scroll-index="Introduction" :style="portraitStyle">
      <img class="portrait" :src="publicPath + data.introduction.portrait" />

      <!-- Bigger screen details -->
      <div class="portrait-details">
        <h1 class="hero-name" :style="nameStyle">{{ data.name }}<span class="stylish-dot">.</span></h1>
        <Menu id="portrait-menu" :items="data.menu" :skewAngle="skewAngle" />
      </div>
    </div>

    <!-- Header -->
    <Skewed class="skew-header section-padding" :angle="skewAngle" :skewContent="true" :color="brandColorDark" :style="headerStyle">
      <h1 class="name">{{ data.name }}<span class="stylish-dot">.</span></h1>
      <Menu id="menu" :items="data.menu" :skewAngle="0" />
      <Introduction :intro="data.introduction"/>
    </Skewed>

    <!-- CV Content -->
    <Skewed class="skew-content" :angle="skewAngle" :color="bkgColor">
      <Content :data="data" :brandColor="brandColor" :skewAngle="skewAngle" />
    </Skewed>

    <!-- Footer -->
    <Skewed class="skew-footer" :angle="skewAngle" :color="brandColor">
      <Footer class="section-padding" :name="data.name" :socials="data.socials" />
    </Skewed>

    <!-- Scroll label -->
    <ScrollLabel class="scroll-label" />
  </div>
</template>

<script>
import Introduction from './components/Introduction.vue'
import ScrollLabel from './components/ScrollLabel.vue'
import Content from './components/Content.vue'
import Skewed from './components/Skewed.vue'
import Footer from './components/Footer.vue'
import Menu from './components/Menu.vue'

// CV data to dynamically load in
import data from "@/cvdata.js"

export default {
  name: 'app',
  components: { Introduction, ScrollLabel, Content, Skewed, Footer, Menu },
  data() {
    return {
      data,
      publicPath: process.env.BASE_URL,
      brandColor: '#1c1c1c',
      brandColorDark: '#0f0f0f',
      bkgColor: '#fff',
      skewAngle: -5
    }
  },
  computed: {
    portraitStyle() {
      return `
        padding-top: ${this.tangent * 50}vw;
        padding-bottom: ${this.tangent * 20}vw;
      `
    },
    headerStyle() {
      return `
        margin-top: -${this.tangent * 55}vw;
      `
    },
    nameStyle() {
      return `
        transform: skewY(${this.skewAngle}deg);
      `
    },
    tangent() {
      return Math.abs(Math.tan(this.skewAngle * Math.PI/180))
    }
  }
}
</script>

